import React, { useState, useEffect } from "react"
import {
  Drawer,
  List,
  ListItem,
  ListItemText,
  Divider,
  Select,
  MenuItem,
  AppBar,
  Toolbar,
  IconButton,
  Typography,
} from "@mui/material"
import MenuIcon from "@mui/icons-material/Menu"
import { Link, navigate } from "gatsby"
import ChotaBheem from "../assets/chotaBheem.png"
import Doraemon from "../assets/Doraemon.png"
import Hagimaru from "../assets/Hagimaru.png"
import Kiteratsu from "../assets/Kiteratsu.png"
import Kochikame from "../assets/Kochikame.png"
import MightyRaju from "../assets/MightyRaju.png"
import NinjaBoy from "../assets/NinjaBoy.png"
import Oswald from "../assets/Oswald.png"
import Shinchan from "../assets/Shinchan.png"
import Perman from "../assets/Perman.png"
import detectiveConan from "../assets/detectiveConan.png"

const shows = [
  { picture: Doraemon, text: "Doraemon", displayText: "Doraemon" },
  {
    picture: ChotaBheem,
    text: "chota bheem pogo",
    displayText: "Chota Bheem",
  },
  { picture: Hagimaru, text: "Hagimaru", displayText: "Hagimaru" },
  { picture: Kiteratsu, text: "Kiteratsu", displayText: "Kiteratsu" },
  { picture: Kochikame, text: "Kochikame", displayText: "Kochikame" },
  { picture: MightyRaju, text: "Mighty Raju", displayText: "Mighty Raju" },
  {
    picture: NinjaBoy,
    text: "Ninja boy Rantaro",
    displayText: "Ninja boy Rantaro",
  },
  { picture: Oswald, text: "Oswald", displayText: "Oswald" },
  { picture: Shinchan, text: "Shinchan", displayText: "Shinchan" },
  {
    picture: detectiveConan,
    text: "Detective Konan",
    displayText: "Detective Konan",
  },
  {
    picture: Perman,
    text: "Perman",
    displayText: "Perman",
  },
]

const languages = ["Telugu", "Tamil", "English", "Hindi"]

const Sidebar = () => {
  const [drawerOpen, setDrawerOpen] = useState(false)
  const [selectedLanguage, setSelectedLanguage] = useState("")

  useEffect(() => {
    const savedLanguage = document.cookie
      .split("; ")
      .find(row => row.startsWith("selectedLanguage="))
    if (savedLanguage) {
      // Convert the cookie value to match the case of the MenuItem values
      const languageValue = savedLanguage.split("=")[1]
      const capitalizedLanguage =
        languageValue.charAt(0).toUpperCase() +
        languageValue.slice(1).toLowerCase()
      setSelectedLanguage(capitalizedLanguage)
    } else {
      setSelectedLanguage(languages[0]) // Default to the first language if none is set
    }
  }, [])

  const toggleDrawer = () => {
    setDrawerOpen(!drawerOpen)
  }

  const handleLanguageChange = event => {
    const newLanguage = event.target.value
    setSelectedLanguage(newLanguage)
    document.cookie = `selectedLanguage=${newLanguage.toLowerCase()};path=/;max-age=31536000` // Save in lowercase to ensure consistency

    const currentPath = window.location.pathname.split("/")
    const currentShow = currentPath[1]

    navigate(`/${currentShow}/${newLanguage.toLowerCase()}`)
    setDrawerOpen(false) // Close the drawer after selecting the language
  }

  return (
    <>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={toggleDrawer}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6">Hungama Shows</Typography>
        </Toolbar>
      </AppBar>
      <Drawer open={drawerOpen} onClose={toggleDrawer}>
        <List>
          {shows.map(show => (
            <ListItem
              button
              key={show.text}
              component={Link}
              to={`/${show.text
                .toLowerCase()
                .replace(/\s+/g, "-")}/${selectedLanguage.toLowerCase()}`}
              onClick={() => setDrawerOpen(false)} // Close the drawer after selecting a show
            >
              <img
                src={show.picture}
                alt={show.displayText}
                style={{ marginRight: "10px" }}
                height={35}
                width={35}
              />
              <ListItemText primary={show.displayText} />
            </ListItem>
          ))}
        </List>
        <Divider />
        <Select
          value={selectedLanguage}
          onChange={handleLanguageChange}
          style={{ margin: "20px", width: "200px" }} // Adjust width if needed
          displayEmpty
        >
          <MenuItem value="" disabled>
            Select Language
          </MenuItem>
          {languages.map(lang => (
            <MenuItem key={lang} value={lang}>
              {lang}
            </MenuItem>
          ))}
        </Select>
      </Drawer>
    </>
  )
}

export default Sidebar
